<template id="tour">
  <v-tour name="myTour" :steps="steps" :options="myOptions"></v-tour>
</template>

<script>
export default {
  name: "Tour",
  data() {
    return {
      myOptions: {
        useKeyboardNavigation: false,
        labels: {
          buttonSkip: "",
          buttonPrevious: "",
          buttonNext: "",
          buttonStop: ""
        }
      },
      steps: [
        {
          target: "#main-game>h3", // We're using document.querySelector() under the hood
          content: `<strong>!</strong> 
            Hexapawn`,
          params: {
            placement: "right"
          }
        },
        //#robot
        {
          target: "#robot",
          content: ` <strong></strong> 
          `,
          params: {
            placement: "top"
          }
        },
        {
          target: "#main-rules>div>h4",
          content:`<strong></strong> 
           1  <strong></strong>`,
          params: {
            placement: "left"
          }
        },
        {
          target: "#main-rules>div>h4",
          content:
            " <strong></strong>  !",
          params: {
            placement: "bottom"
          }
        }
      ],
    };
  },
  mounted: function() {
    this.$tours["myTour"].start();
  }
};
</script>

